import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import styles from './Faq.module.css';

const useStyles = makeStyles((theme) => ({
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },
}));


const faqLibrary = [

    { id: 1, question: "What is a coronavirus ?", content: "Coronaviruses are a large family of viruses which may cause illness in animals or humans. In humans, several coronaviruses are known to cause respiratory infections ranging from the common cold to more severe diseases such as Middle East Respiratory Syndrome(MERS) and Severe Acute Respiratory Syndrome(SARS).The most recently discovered coronavirus causes coronavirus disease COVID - 19." },

    { id: 2, question: "What is COVID - 19 ?", content: "COVID - 19 is the infectious disease caused by the most recently discovered coronavirus.This new virus and disease were unknown before the outbreak began in Wuhan, China, in December 2019. COVID - 19 is now a pandemic affecting many countries globally." },

    { id: 3, question: "How does COVID - 19 spread ?", content: "People can catch COVID - 19 from others who have the virus. The disease spreads primarily from person to person through small droplets from the nose or mouth, which are expelled when a person with COVID - 19 coughs, sneezes, or speaks.These droplets are relatively heavy, do not travel far and quickly sink to the ground. People can catch COVID - 19 if they breathe in these droplets from a person infected with the virus. This is why it is important to stay at least 1 metre (3 feet) away from others.These droplets can land on objects and surfaces around the person such as tables, doorknobs and handrails. People can become infected by touching these objects or surfaces, then touching their eyes, nose or mouth. This is why it is important to wash your hands regularly with soap and water or clean with alcohol - based hand rub." },

];

function Faq() {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    return (
        <div className={styles.container}>
            <div className={styles.content}>
                {faqLibrary.map((faq, i) => (
                    <ExpansionPanel expanded={expanded === 'panel' + i} onChange={handleChange('panel' + i)}>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls={"panel"+i+"bh-content"}
                            id={"panel"+i+"bh-header"}
                        >
                            <Typography className={classes.heading}>{faq.question}</Typography>
                        </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Typography>{faq.content}</Typography>
                    </ExpansionPanelDetails>
                    </ExpansionPanel>
                ))}
        </div>
        </div >
    );
}

export default Faq;