import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import styles from './Precautions.module.css';

const useStyles = makeStyles((theme) => ({
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },
}));

function Precautions() {
    const classes = useStyles();

    return (
        <div className={styles.container}>
            <div className={styles.content}>
                <Typography>Protect yourself and others around you by knowing the facts and taking appropriate precautions. Follow advice provided by your local public health agency.</Typography>
                <ul>
                    <li>Clean your hands often. Use soap and water, or an alcohol-based hand rub.</li>
                    <li>Maintain a safe distance from anyone who is coughing or sneezing.</li>
                    <li>Donâ€™t touch your eyes, nose or mouth.</li>
                    <li>Cover your nose and mouth with your bent elbow or a tissue when you cough or sneeze.</li>
                    <li>Stay home if you feel unwell.</li>
                    <li>If you have a fever, a cough, and difficulty breathing, seek medical attention. Call in advance.</li>
                    <li>Follow the directions of your local health authority.</li>
                </ul>

                <Typography>Avoiding unneeded visits to medical facilities allows healthcare systems to operate more effectively, therefore protecting you and others.</Typography>
            </div>
        </div >
    );
}

export default Precautions;