import HomeIcon from '@material-ui/icons/Home';
import RoomIcon from '@material-ui/icons/Room';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import HelpIcon from '@material-ui/icons/Help';

import homePage from '../pages/Home/Home';
import IndiaPage from '../pages/India/India';
import FAQPage from '../pages/FAQ/Faq';
import PrecautionsPage from '../pages/Precautions/Precautions'


const Routes = [
  {
    path: '/covid19tracker',
    sidebarName: 'Home',
    navbarName: 'Home',
    icon: HomeIcon,
    component: homePage
  },
  {
    path: '/covid19tracker/india',
    sidebarName: 'India Page',
    navbarName: 'India Page',
    icon: RoomIcon,
    component: IndiaPage
  },
  {
    path: '/covid19tracker/precautions',
    sidebarName: 'Precautions',
    navbarName: 'Precautions',
    icon: AddCircleIcon,
    component: PrecautionsPage
  },
  {
    path: '/covid19tracker/faq',
    sidebarName: 'FAQ',
    navbarName: 'FAQ',
    icon: HelpIcon,
    component: FAQPage
  }
];

export default Routes;